const flywheel = {
  greet() {
    const url = 'https://mobile.flywheel.com/search?by=location&filter=hailable&latitude=-0.00263777705226216' +
    '&longitude=-0.001884127044582407&auth_token=(null)';

    fetch(url).then(r => r.json())
    .then(data => console.log(data))
    .catch(e => console.log('Booo'));
  }
};

export default flywheel;



